import React from 'react'
import styled from 'styled-components'

const BurgerMenu = ({ handleBurgerClick, isMobileMenuOpen }) => {
  return (
    <Wrapper onClick={handleBurgerClick}>
      <div className={isMobileMenuOpen ? 'open' : ''}>
        <span>&nbsp;</span>
        <span>&nbsp;</span>
        <span>&nbsp;</span>
      </div>
    </Wrapper>
  )
}

export default BurgerMenu

const Wrapper = styled.div`
  position: relative;
  padding-top: .7rem;
  cursor: pointer;

  & span {
    background: white;
    display: block;
    position: relative;
    width: 3.5rem;
    height: .4rem;
    margin-bottom: .7rem;
    transition: all ease-in-out .2s;
  }

  .open span:nth-child(2) {
    opacity: 0;
  }

  .open span:nth-child(1) {
    transform: rotate(-45deg);
    top: 11px;
  }

  .open span:nth-child(3) {
    transform: rotate(45deg);
    top: -11px;
  }
`